<template>
	<div>
		<el-dialog
			:title="title"
			:visible.sync="mateVisible">
			<el-table
				:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
				:data="mateInfoList"
				:empty-text="$t('common.nodata')"
				border>									
				<el-table-column
					:label="$t('common.No')"
		      type="index"
		      width="50">
		    </el-table-column>
				<el-table-column
					prop="shelf_id"
				  :label="$t('storage.shelf_id')">						
				</el-table-column>				
					<el-table-column
					prop="label_code"
				  	:label="$t('storage.label_code')">						
				</el-table-column>
				<el-table-column
					prop="quantity"
				  	:label="$t('storage.quantity')">						
				</el-table-column>
				<el-table-column
					prop="position"
				  	:label="$t('storage.position')">					
				</el-table-column>	
				<el-table-column
			  	prop=""
			  	:label="$t('common.operation')"
			  	width="98">
			  	<template slot-scope="scope">
			  		<el-button type='primary' size="mini"  @click="takeOut(scope.row,scope.$index)" :disabled="scope.row.state? true:false">{{$t('storage.take_out')}}</el-button>
			  	</template>				 
				</el-table-column>
			</el-table>
		</el-dialog>
		<v-outbyposition ref="outbyposition" v-on:changeList="changeList"></v-outbyposition>
	</div>
</template>
<script>
 import VOutbyposition from '../materialcommon/outbyposition'
	export default{
		components:{ VOutbyposition },
		data(){
			return{
				title:'',
				mateVisible:false,
				mateInfoList:[],
				spliceIndex:''
			}
		},
		methods:{
			open(value){
				this.mateVisible = true
				this.mateInfoList = value.mate_info
				this.title =this.$t('storage.part_num')+': '+ value.part_num
			},
			takeOut(value,index){
				this.spliceIndex = index
				this.$refs.outbyposition.open(value)
			},
			changeList(){
				this.mateInfoList.splice(this.spliceIndex,1)
			}
		}
		
	}
</script>


