<template>
	<div class="storage_content">
		<div class="typesearch">
		<div class="typecontent">
		<!-- 库位 -->
			<!-- 库位 -->
		<div class="operation_select input_wrap">
			<el-select value-key="id" v-model="searchform.warehouse_category_id" @change="categoryTypeChange">
	    	<el-option
		      	v-for="item in categoryTypeList"
		      	:key="item.id"
		      	:label="item.category_name"
		      	:value="item.id">										      
	    	</el-option>
			</el-select>
		</div>
			<div class="operation_select input_wrap">
				<el-select value-key="id" v-model="searchform.warehouse_name_id" >
		    	<el-option
			      	v-for="item in warehouselist"
			      	:key="item.id"
			      	:label="item.warehouse_name"
			      	:value="item.id">										      
		    	</el-option>
				</el-select>
			</div>
			<!-- 物料搜索条件 -->
			<div class="operation_select input_wrap">
				<el-select value-key="type_id" v-model="type_id">
		    	<el-option
		    		change
		      	v-for="item in options"
		      	:key="item.type_id"
		      	:label="item.name"
		      	:value="item.type_id">
		    	</el-option>
		  	</el-select>
			</div>
			<div class="input_wrap">
<!-- 				<span class="input_span">{{$t('storage.supplier_name')}}</span> -->
				<el-input v-model="searchcontent"></el-input>
			</div>
			<div class="input_wrap">
				<el-button type="primary" size="mini" @click="search">{{$t('common.search')}}</el-button>
				<el-button type="primary" plain size="mini" @click="refresh">{{$t('common.refresh')}}</el-button>
				<el-button type="success"  size="mini" @click="download" class="downloadexc"><i class="el-icon-download el-icon--right"></i>{{$t('dashboard.downloadExcel')}}</el-button>
			</div>
		</div>
		<div class="lightscomp">
			<v-lights></v-lights>
		</div>
	</div>
		<div>
			<template>
			<el-table
				:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
				:data="matlist"
				:empty-text="$t('common.nodata')"
				border>									
				<el-table-column
					:label="$t('common.No')"
		      type="index"
		      width="50">
		    </el-table-column>
				<el-table-column
					prop="part_num"
					:show-overflow-tooltip='true'
				  :label="$t('storage.part_num')">	
				  </el-table-column>					
				<el-table-column
					:show-overflow-tooltip='true'
					prop="description"
				  	:label="$t('storage.description')">						
				</el-table-column>
				<el-table-column
					prop="all_quantity"
				  :label="$t('storage.all_quantity')">							
				</el-table-column>	
				<el-table-column
					prop="all_reel"
				  	:label="$t('storage.all_reel')">						
				</el-table-column>
				<el-table-column
			  	prop=""
			  	width="98">
			  	<template slot-scope="scope">
			  	<el-button icon="el-icon-search" circle size="mini" @click="showMatelist(scope.row)"></el-button>	  		
			  	</template>				 
				</el-table-column>		
				<el-table-column
			  	prop=""
			  	:label="$t('common.operation')"
			  	width="98">
			  	<template slot-scope="scope">
			  		<el-button type='primary' size="mini"  @click="takeOut(scope.row)" :disabled="scope.row.state? true:false">{{$t('storage.take_out')}}</el-button>
			  	</template>				 
				</el-table-column>
			</el-table>
		</template>
		<v-mate ref="mate"></v-mate>
		<v-takeout ref ="takeout" v-on:getMaterialList="getMaterialList"></v-takeout>
		</div>
		<v-pagination :totalNum="totalNum" v-on:listenPagesize="changePagesize" v-on:listenCurrentpage="changeCurrent"></v-pagination>
	</div>
</template>

<script>
import VLights from '../materialcommon/lights'
import VPagination from '../../common/pagination'
import VMate from  './mate'
import VTakeout from './takeout'
import {searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf'
import {groupByInfo, searchByInfo, outByPosition, exportMateInfoByInfo} from '@/request/api/material'
import { genUrl } from '@/utils/public'
	export default{
		components:{VLights,VPagination,VMate,VTakeout},
		data() {
			return{
				loading:false,
				dialogVisible:false,
				matlist:[],
				totalNum:0,
				searchform:{
					part_num:'',
					warehouse_category_id:0,
					warehouse_name_id:0,
					description:'',
					start:0,
					rows:5,
				},
				options:[
					{name:this.$t('storage.description'),type_id:'description'},
					{name:this.$t('storage.part_num'),type_id:'part_num'},
				],
				type_id:'',
				searchcontent:'',
				categoryTypeList:[{'category_name':'所有','id':0}],
				warehouselist:[{'warehouse_name':'所有','id':0}],
				warehouse_name_id:'',
				timer:null,
				order_id:'',
				title:'',
				materialNum:'',
				params:'',
			}
		},
		beforeDestroy(){
			if(this.timer) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer); //关闭
    	}
		},
		mounted() {
			this.getCategoryType()
			this.getMaterialList()
		},
		methods:{
			// 获取列表
			getMaterialList() {
				groupByInfo(this.searchform).then(res =>{
          if(res.result){
            this.matlist = res.mate_list
            this.totalNum = res.rows
          }else{
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
				})
			},
			refresh(){
				this.$emit('getMaterialList')
				this.searchcontent = ''
				this.type_id = ''
				this.searchform.warehouse_category_id = 0
				this.searchform.warehouse_name_id = 0
				this.searchform.part_num = ''
				this.searchform.description = ''
				this.searchform.start =0
				this.searchform.rows =5
				this.getMaterialList()
			},
			// 获取仓库类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	this.warehouselist = [{'warehouse_name':'所有','id':0}]
	    	this.searchform.warehouse_name_id = 0
	    	searchWarehouseNameByCategory({warehouse_category_id:this.searchform.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouselist = this.warehouselist.concat(res.data)
	    		}
	    	})
	    },
	    categoryTypeChange(){
	    	this.searchform.warehouse_name_id = 0
	    	this.getWarehouseList()
	    },
			changePagesize(size){
				this.searchform.rows = size
				this.getMaterialList()
			},
			changeCurrent(page){
				this.searchform.start = (page-1)*this.searchform.rows
				this.getMaterialList()
			},
			// 查询
			search() {
				if (this.type_id == 'part_num') {
					this.searchform.part_num= this.searchcontent
				} else {
					this.searchform.description = this.searchcontent
				}
				this.getMaterialList()
			},
			showMatelist(value){
				// this.mateInfo = value.mate_info
				this.$refs.mate.open(value)
			},
			// 取料
			takeOut(value) {
				this.$refs.takeout.open(value)
			},
			sure(){		
				let	_this = this
				this.params.order_id = this.order_id
				this.params.warehouse_name_id = this.warehouse_name_id
				outByPosition(this.params).then(res =>{
					if (res.result) {
						// this.getMaterialList()
						this.dialogVisible = false	
						this.timer = window.setTimeout(function(){
						 _this.getMaterialList() }, 1000);
					}else{
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}
				})
			},		
			//下载报表
			download(){
				exportMateInfoByInfo({warehouse_category_id:this.searchform.warehouse_category_id,warehouse_name_id:this.searchform.warehouse_name_id,part_num:this.searchform.part_num,description:this.searchform.description}).then(res =>{
					const url = genUrl(res, {});//{}指的是表头，res是后台返回来的数据
	        const isIE = false || !!document.documentMode || "ActiveXObject" in window;
	        if (isIE) {
	        	// ie6-11
	        	navigator.msSaveOrOpenBlob(new Blob([url], {type: "text/csv"}),'物料统计信息报表.csv')
	        }else if (!isIE && !!window.StyleMedia) {
	        	//edge 20+
	        	navigator.msSaveOrOpenBlob(new Blob([url], {type: "text/csv"}),'物料统计信息报表.csv')
	        }
	        else{
	        	//chrome, firefox
	        	const a = document.createElement('a');
	        	a.href = url;
	        	a.download = "物料统计信息报表.csv";
	        	a.click();
	        	window.URL.revokeObjectURL(url);
	        }
				})
			}
		},
	}
</script>
<style scoped>
.hh .el-dialog__body{
	padding: 40px;
}
.el-select-dropdown__wrap{
	margin-bottom: 0px;
}
.el-input__icon{
	line-height: 30px;
}
.take_mat_span{
	background: #28a745;
	padding:6px 8px;
	border-radius: 30%;
	color: #fff;
}
.stay_mat_span{
	background: #e6a23c;
	padding:6px 8px;
	border-radius: 30%;
	color: #fff;
}
.storagedetail_input{
}
.storagedetail_title{
	display: block;
	text-align: left;
	height: 40px;
	line-height: 40px;
}
.typesearch .el-select{
	width:160px;
}

.typesearch{
		display: flex;
		font-size: 14px;
		margin:4px 10px;
	}
	.typesearch .el-input__inner{
		width:160px;
		height: 40px;
	}
	.typesearch .el-input{
		height: 40px;
		width:160px;
	}
	.typecontent{
		display: flex;
		flex-wrap: wrap;
		flex:1;
	}
	.lightscomp{
		width:300px;
	}
	.input_wrap{
	display: flex;
	padding:6px;
	}
	.input_span{
		display: inline-block;
		width:92px;
		height: 40px;
		line-height: 40px;
	}
	.input_wrap .el-button{
		padding:4px 12px;
		height: 40px;
		width:60px;
	}
	.input_wrap .el-select-dropdown__item{
		font-size: 12px;
	}
	.input_wrap .downloadexc{
		width:96px;
	}
</style>


