<template>
	<el-dialog
	  :title="title"
	  :visible.sync="takeoutVisible"
	  width="30%">
	  <div class="storagedetail_input">
	  	<span class="storagedetail_title">{{$t('storage.order_code')}}</span>
	  	<el-input v-model="dataform.order_id" :placeholder="$t('storage.orderidtips')"></el-input>
	  </div>
	  <div class="storagedetail_input">
	  	<span class="storagedetail_title">{{$t('storage.category_name')}}</span>
		  	<el-select value-key="id" v-model="dataform.warehouse_category_id" @change="categoryTypeChange">
		    	<el-option
		      v-for="item in categoryList"
		      :key="item.id"
		      :label="item.category_name"
		      :value="item.id">
		    </el-option>
		  	</el-select>
	  </div>
	  <div class="storagedetail_input">
	  	<span class="storagedetail_title">{{$t('storage.warehouse_name')}}</span>
		  	<el-select value-key="id" v-model="dataform.warehouse_name_id">
		    	<el-option
		      v-for="item in warehouseList"
		      :key="item.id"
		      :label="item.warehouse_name"
		      :value="item.id">
		    </el-option>
		  	</el-select>
	  </div>
	  <div class="storagedetail_input">	
	  	<span class="storagedetail_title">{{$t('storage.warehouse_name')}}</span>  	
		  	<el-checkbox-group
					v-model="dataform.shelf_list">	  						
					<el-checkbox v-for="shelf in shelflist" :label="shelf" :key="shelf">{{shelf}}</el-checkbox>  						
				</el-checkbox-group>			
	  </div>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="takeoutVisible = false" size="small">取 消</el-button>
	    <el-button type="primary" @click="sure" size="small">确 定</el-button>
	  </span>
	</el-dialog>
</template>
<script>
  import { outByPN } from '@/request/api/material'
	import {searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf'
	export default{
		data(){
			return{
				title:'',
				takeoutVisible:false,
				categoryList:[{'category_name':'其他','id':-1}],
				warehouseList:[{'warehouse_name':'其他','id':-1}],
				dataform:{
					warehouse_name_id:'',
					warehouse_category_id:'',
					order_id:'',
					shelf_list:[],
					color:this.$store.state.color,
					part_num:''
				},
				shelflist:[],
				
			}
		},
		methods:{
			open(value){
				this.title = this.$t('storage.part_num')+':'+ value.part_num
				this.dataform.part_num = value.part_num
				this.shelflist = value.shelf_list
				this.takeoutVisible = true
				this.dataform.order_id = ''
				this.dataform.warehouse_category_id = -1
				this.dataform.warehouse_name_id = -1
				this.dataform.shelf_list = []
				searchWarehouseCategory({}).then(res =>{
					if (res.result) {
						this.categoryList = this.categoryList.concat(res.data_list)
					} else {
						this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
					}
				})
			},
			categoryTypeChange(){
				this.warehouseList = [{'warehouse_name':'其他','id':-1}]
				this.warehouse_name_id = -1
	    	searchWarehouseNameByCategory({warehouse_category_id:this.dataform.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouseList = this.warehouseList.concat(res.data)
	    		}
	    	})
			},
			sure(){
				outByPN(this.dataform).then(res =>{
					let _this = this
					if (res.result) {
						this.takeoutVisible = false
						this.$message({
							showClose:true,
							message:'success',
							type:'success'
						})
						let Timer=window.setTimeout(function(){
							_this.$emit('getMaterialList')
						},1500)
						
					} else {
						this.$message({
							showClose:true,
							message:res.msg,
							type:'error'
						})
					}
				})
			}
		}
	}
</script>
<style scoped>
	.storagedetail_input{
	text-align: left;
}
.storagedetail_title{
	display: block;
	text-align: left;
	height: 40px;
	line-height: 40px;
}
.storagedetail_input .el-select{
	width:100%;
}
</style>


